body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: black;
  color: white;
  margin: 0 auto;
  font-family: 'Roboto', sans-serif;
}

.anchor {
  display: block;
  position: relative;
  top: -100px;
  visibility: hidden;
}
#contact {
  padding: 20px 0 0 0;
  margin-left: auto;
  margin-right: auto;
  background: #ac4315;
}

@media screen and (max-width: 750px) {
  #contact {
    margin-left: 0;
    margin-right: 0;
  }
}

#contact a {
  color: white;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

#contact a:hover {
  background: black;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

#contact p {
  background-color: #ac4315;
  padding: 20px 10%;
}

#contact hr {
  width: 20%;
  height: 5px;
  background: white;
  border: 0;
  margin: 0 auto;
}
.footer-wrapper {
  text-align: center;
  background: #ac4315;
  padding-top: 20px;
}

.footer-wrapper p {
  margin: 0;
  padding: 0 0 50px 0;
}
.header {
  background: black;
  color: white;
  height: 70px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding-left: 10%;
}

@media screen and (max-width: 750px) {
  .header {
      padding-left: 0;
      text-align: center;
  }
}

.menu {
  list-style-type: none;
  margin-left: 55%;
  margin-top: -55px;
}

.menu li {
  float: left;
}

.menu li a {
  padding: 0 20px;
  color: white;
  text-decoration: none;
  border-left: 1px solid white;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.menu li a:hover { 
  background: white;
  color: black;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}


@media screen and (max-width: 750px) { 
  .menu { 
      display: table;
      margin: 0 auto;
  }

  .menu li { 
      background: black;
  }
}
.intro-wrapper { 
  margin-top: 70px;
  padding: 20px 10% 60px 10%;
  background-color: #4382A9;
}

.intro {
  display: flex;
  flex-flow: wrap;
}
.intro-text-container {
  flex-basis: 33%;
}

@media screen and (max-width: 750px) {
  .intro-text-container {
    flex-basis: 100%;
    order: 1;
  }
}

.intro-text-container a {
  color: white;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.intro-text-container a:hover {
  background: black;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.reference-container {
  flex-direction: column;
  flex-basis: 30%;
  display: flex;
  flex-wrap: wrap;
  margin-left: 10px;
  padding: 60px 5px 20px 5px;
}

@media screen and (max-width: 900px) {
  .reference-container {
    flex-basis: 100%;
    margin: 10px auto 0 auto;
    flex-direction: row;
    justify-content: space-evenly;
    padding: 5px 5px 20px 5px;
  }
}

@media screen and (max-width: 750px) {
  .reference-container {
    order: 3;
  }
}

.reference-style {
  margin-top: 10px;
  height: 30px;
  width: auto;
}

.link-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.link-container a {
  flex-basis: 10%;
}

.link-text {
  margin-left: 10px;
  color: white;
  font-size: 16px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  -webkit-text-decoration: underline white;
          text-decoration: underline white;
}

.link-text:hover {
  background: black;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.headshot-container {
  flex-basis: 30%;
  padding: 10px 20px 20px 0;
}

@media screen and (max-width: 750px) {
  .headshot-container {
    flex-basis: 70%;
    margin: 10px auto 0 auto;
    order: 2;
  }
}

.headshot-container .headshot {
  width: 100%;
  height: auto;
  border-radius: 3px;
  box-shadow: 5px 5px 5px #181818;
}
.projects-and-work-container {
  padding: 20px 0 80px 0;
  background-color: rgba(129, 198, 131, 0.8);
}

.project-list-container {
  display: flex;
  flex-wrap: wrap;
}

.secondary-projects-container {
  display: flex;
  flex-wrap: wrap;
  margin: 50px auto 0 auto;
  width: 80%;
}

@media screen and (max-width: 750px) {
  .secondary-projects-container {
    width: 90%;
  }
}
.project {
  transition: 0.5s;
  -webkit-transition: 0.5s;
  padding: 0 10%;
}

@media screen and (max-width: 750px) {
  .project {
    flex-basis: 100% !important;
  }
}

.title { 
  font-size: 1.5em;
}

.project-title-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.project-title-container img {
  height: 50px;
  width: auto;
  margin-right: 10px;
}

.project hr {
  background: white;
  width: 50px;
  height: 2px;
  margin: 0;
  border: 0;
}

.display {
  display: flex;
  flex-wrap: column;
  justify-content: center;
  margin-top: 15px;
}

.phone-only-display {
  display: flex;
  flex-wrap: column;
  justify-content: space-evenly;
  margin-top: 15px;
}

.laptop-only-display {
  display: flex;
  flex-wrap: column;
  justify-content: space-evenly;
  margin-top: 15px;
}

.project-link {
  text-align: center;
}

.project-link a {
  text-align: center;
  font-size: 20px;
  padding: 5px 10px;
  background: none;
  border: 2px solid white;
  color: white;
  margin: 0 auto;
  border-radius: 5px;
  width: 120px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  margin-bottom: 10px;
  text-decoration: none;
  transition: 0.3s;
}

.project-link a:hover {
  background: white;
  color: black;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.project-text, .project-tech {
  width: 60%;
  margin: 2vh auto 0 auto;
  text-align: center;
}

@media screen and (max-width: 750px) {
  .project-text, .project-tech {
    width: 100%;
  }
}

.laptop {
  flex-basis: 65%;
  border-radius: 2vw 2vw 1vw 1vw;
  margin-right: 20px;
}

.top {
  height: 30vw;
  width: 95%;
  background: rgb(33, 38, 37);
  border-bottom: 1px black solid;
  box-shadow: 5px 5px 5px black;
  padding-top: 0.5vw;
  border-radius: 2vw 2vw 0 0;
  margin-right: auto;
  margin-left: auto;
}

.laptop .screen {
  width: 99%;
  margin-left: auto;
  margin-right: auto;
  height: 32vw;
  background: black;
  border-radius: 2vw 2vw 0 0;
}

.keyboard {
  height: 1vw;
  background: rgb(82, 85, 91);
  background: -webkit-linear-gradient(left, rgba(160, 167, 180, 1) 0%, rgba(82, 85, 91, 1) 4%, rgba(82, 85, 91, 1) 96%, rgba(160, 167, 180, 1) 100%);
  background: linear-gradient(90deg, rgba(160, 167, 180, 1) 0%, rgba(82, 85, 91, 1) 4%, rgba(82, 85, 91, 1) 96%, rgba(160, 167, 180, 1) 100%);
  width: 100%;
}

.bottom {
  height: 0.8vw;
  background: rgb(33, 38, 37);
  background: -webkit-linear-gradient(left, rgba(33, 38, 37, 1) 0%, rgba(54, 62, 60, 1) 4%, rgba(54, 62, 60, 1) 96%, rgba(33, 38, 37, 1) 100%);
  background: linear-gradient(90deg, rgba(33, 38, 37, 1) 0%, rgba(54, 62, 60, 1) 4%, rgba(54, 62, 60, 1) 96%, rgba(33, 38, 37, 1) 100%);
  border-radius: 0 0 4vw 4vw;
  width: 100%;
  box-shadow: 5px 5px 5px black;
}

.lifter {
  height: 0.8vw;
  width: 25%;
  border-radius: 0 0 2vw 2vw;
  margin-left: auto;
  margin-right: auto;
  background: #a7adba;
  background: -webkit-linear-gradient(left, rgba(97, 101, 108, 1) 0%, rgba(201, 209, 226, 1) 4%, rgba(201, 209, 226, 1) 96%, rgba(97, 101, 108, 1) 100%);
  background: linear-gradient(90deg, rgba(97, 101, 108, 1) 0%, rgba(201, 209, 226, 1) 4%, rgba(201, 209, 226, 1) 96%, rgba(97, 101, 108, 1) 100%);
}

.laptop .screenshot {
  width: 90%;
  height: auto;
  margin-left: calc((100% - 90%) / 2);
  margin-right: calc((100% - 90%) / 2);
  margin-top: calc((34vw - 31vw) / 2);
}
.phone {
  flex-basis: 15%;
  height: 27vw;
  margin-top: auto;
  margin-bottom: auto;
  border: 1px #2f4f4f solid;
  background: -webkit-linear-gradient(top, #707070 0%, #3F3F3F 100%);
  background: linear-gradient(to bottom, #707070 0%, #3F3F3F 100%);
  border-radius: 2.2vw;
  box-shadow: 2px 2px 5px black;
}

.ringer {
  height: 1vw;
  width: 3px;
  margin-top: -23.5vw;
  background: #696969;
  margin-left: -0.25vw;
  border-radius: 1px;
  border: 1px #2f4f4f solid;
}

@media screen and (max-width: 750px) {
  .ringer {
    margin-left: -0.5vw;
    width: 1px;
  }
}

.volume-button {
  height: 2.7vw;
  width: 2px;
  background: #696969;
  margin-left: -0.25vw;
  border-radius: 3px;
  border: 1px #2F4F4F solid;
}

@media screen and (max-width: 750px) {
  .volume-button {
    margin-left: -0.5vw;
    width: 1px;
  }
}

.up {
  margin-top: 1vw;
}

.down {
  margin-top: 0.5vw;
}

.power-button {
  height: 2.5vw;
  width: 2px;
  background: #696969;
  margin-top: -6.5vw;
  margin-left: auto;
  margin-right: -0.25vw;
  border-radius: 3px;
  border: 1px #2F4F4F solid;
  box-shadow: 2px 2px 5px black;
}

@media screen and (max-width: 750px) {
  .power-button {
    margin-right: -0.5vw;
    width: 1px;
  }
}

.phone .screen {
  flex-basis: 20%;
  height: 26.5vw;
  margin-top: 0.25vw;
  margin-left: 0.25vw;
  margin-right: 0.25vw;
  border-radius: 2.2vw;
  background: black;
}

.phone .screenshot {
  width: 100%;
  height: auto;
  margin-top: 3vw;
}

.home-button {
  height: 2vw;
  width: 2vw;
  margin-top: calc(26.5vw - 8.5vw - 1.8vw);
  margin-right: auto;
  margin-left: auto;
  background: gray;
  border-radius: 50%;
  border: 2px #616161 outset;
  border-right: 2px solid #343434;
  border-bottom: 2px solid #343434;
  background: black;
}

@media screen and (max-width: 900px) {
  .home-button {
    margin-top: calc(26.5vw - 8.5vw - 2vw);
    height: 1.5vw;
    width: 1.5vw;
  }
}
.other-project {
  flex-basis: 45%;
  border-radius: 2px;
  margin-right: 5px;
  margin-bottom: 5px;
  padding: 0 10px 15px 10px;
  background: rgba(233, 233, 233, 0.6);
  color: black;
}

@media screen and (max-width: 750px) {
  .other-project {
    flex-basis: 100%;
  }
}

.other-project hr {
  width: 25%;
  margin: 0;
  height: 3px;
  background: black;
  border: 0;
}

.other-project h3 {
  margin-bottom: 5px;
}

.project-link {
  padding-top: 20px;
}

.other-project .project-link a {
  text-align: center;
  font-size: 20px;
  padding: 5px 10px;
  background: none;
  margin: 0 auto;
  border-radius: 5px;
  width: 120px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  margin-bottom: 10px;
  text-decoration: none;
  border: 2px solid black;
  color: black;
  transition: 0.3s;
}

.other-project .project-link a:hover {
  border: 2px solid white;
  color: white;
  background: black;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
